import React from "react";
import bloglist from "../../editable-stuff/blog";
import { Link } from "react-router-dom";
import {
  Container,
  Typography,
  Card,
  CardContent,
  CardMedia,
  Button,
  Grid,
  Box,
} from "@mui/material";

const Blog = () => {
  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        mt: 8,
        px: 2,
      }}
    >
      <Box
        sx={{
          width: "100%",
          maxWidth: "1280px", // 👈 Set the max width
        }}
      >
        <Typography variant="h3" align="center" gutterBottom>
          Blogs
        </Typography>
        {bloglist.map((value, index) => (
          <BlogCard
            key={index}
            title={value.title}
            image={value.image}
            description={value.description}
            index={index}
          />
        ))}
      </Box>
    </Box>
  );
};

const BlogCard = ({ index, title, image, description }) => {
  return (
    <Card sx={{ mb: 5, boxShadow: 2 }}>
      <Grid container>
        {image && (
          <Grid item xs={12} md={4}>
            <CardMedia
              component="img"
              image={image}
              alt={title}
              sx={{ height: "100%", objectFit: "fill" }}
            />
          </Grid>
        )}
        <Grid item xs={12} md={image ? 8 : 12} sx={{ padding: 2 }}>
          <CardContent>
            <Typography variant="h5" gutterBottom>
              {title}
            </Typography>
            <Typography variant="body1" color="text.secondary" paragraph>
              {description}
            </Typography>
            <Box textAlign="left">
              <Button
                component={Link}
                to={`/blog/${index}`}
                variant="contained"
                size="small"
              >
                Read More
              </Button>
            </Box>
          </CardContent>
        </Grid>
      </Grid>
    </Card>
  );
};

export { Blog };
